"use client";

import { useState, useEffect, useRef } from "react";
import { Button } from "@/components/ui/button";

type Section = { heading: string; content: string };

type GameDetailProps = {
  title: string;
  subtitle: string;
  description: string;
  image: string;
  developer: string;
  iframeSrc?: string;
  overview: React.ReactNode;
  whatIs: React.ReactNode;
  howToPlay: React.ReactNode;
  sections?: Section[];
  children?: React.ReactNode;
};

export default function GameDetail({
  title = "Perfect Tidy Online",
  subtitle,
  description,
  iframeSrc,
  overview,
  whatIs,
  howToPlay,
  sections,
  children,
}: GameDetailProps) {
  const [isFullscreen, setIsFullscreen] = useState(false);
  const [rating, setRating] = useState(0);
  const [hoverRating, setHoverRating] = useState(0);
  const [showShareModal, setShowShareModal] = useState(false);
  const [showComments, setShowComments] = useState(false);
  const containerRef = useRef<HTMLDivElement>(null);

  const toggleFullscreen = () => {
    if (!isFullscreen && containerRef.current) {
      containerRef.current.requestFullscreen?.().catch(err => {
        console.error(`Error attempting to enable fullscreen: ${err.message}`);
      });
    } else {
      document.exitFullscreen?.();
    }
  };

  const handleShare = () => {
    if (navigator.share) {
      navigator.share({
        title: `Play ${title} Online`,
        text: `Experience ${title} directly in your browser!`,
        url: window.location.href,
      });
    } else {
      setShowShareModal(true);
    }
  };

  const handleComment = () => {
    setShowComments(!showComments);
  };

  const copyToClipboard = async () => {
    try {
      await navigator.clipboard.writeText(window.location.href);
      alert("Link copied to clipboard!");
    } catch (err) {
      console.error('Failed to copy: ', err);
    }
  };

  // ÁõëÂê¨ÂÖ®Â±èÁä∂ÊÄÅÂèòÂåñ
  useEffect(() => {
    const handleFullscreenChange = () => {
      setIsFullscreen(!!document.fullscreenElement);
    };

    document.addEventListener('fullscreenchange', handleFullscreenChange);
    return () => {
      document.removeEventListener('fullscreenchange', handleFullscreenChange);
    };
  }, []);

  return (
    <div className="max-w-5xl mx-auto px-4 py-12">
      {/* Ê∏∏ÊàèÊ†áÈ¢ò + iframe */}
      {iframeSrc && (
        <div className="mb-12">
          <h1 className="text-4xl font-bold text-blue-600 mb-4">
            {title} ‚Äì Play Online Free
          </h1>
          
          {/* Ê∏∏ÊàèÂÆπÂô® */}
          <div 
            ref={containerRef}
            className={`relative bg-green-100 rounded-lg shadow-lg overflow-hidden ${
              isFullscreen ? 'fixed inset-0 z-50 rounded-none' : 'h-[700px]'
            }`}
            style={{ background: 'linear-gradient(to bottom, #d1f2eb, #a9dfbf)' }}
          >
            {/* È°∂ÈÉ®ÊéßÂà∂Êù° - ÂÆåÂÖ®ÊåâÁÖßÂõæÁâáÊ†∑Âºè */}
            <div className="absolute top-0 left-0 right-0 z-10 flex justify-between items-center p-4 bg-gradient-to-r from-purple-600 to-blue-600 text-white shadow-md h-16">
              <div className="flex items-center space-x-3">
                <div className="w-10 h-10 bg-white rounded-full flex items-center justify-center shadow-sm">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    fill="none"
                    viewBox="0 0 24 24"
                    strokeWidth={2}
                    stroke="currentColor"
                    className="w-6 h-6 text-purple-600"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      d="M9 8h6m-3-3v6m9 3a9 9 0 11-18 0 9 9 0 0118 0z"
                    />
                  </svg>
                </div>
                <div>
                  <h3 className="font-bold text-lg">Play {title}</h3>
                  <p className="text-sm opacity-80">Online Game</p>
                </div>
              </div>
              
              {/* Êìç‰ΩúÊåâÈíÆ - ÂÆåÂÖ®ÊåâÁÖßÂõæÁâáÊ†∑Âºè */}
              <div className="flex space-x-2">
                {/* Êõ¥Â§öÈÄâÈ°πÊåâÈíÆÔºà‰∏â‰∏™ÁÇπÔºâ */}
                <button className="p-2 bg-white bg-opacity-20 rounded-full hover:bg-opacity-30 transition">
                  <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                    <path d="M6 10a2 2 0 11-4 0 2 2 0 014 0zM12 10a2 2 0 11-4 0 2 2 0 014 0zM16 12a2 2 0 100-4 2 2 0 000 4z" />
                  </svg>
                </button>
                
                {/* ÂàÜ‰∫´ÊåâÈíÆ */}
                <button 
                  onClick={handleShare}
                  className="p-2 bg-white bg-opacity-20 rounded-full hover:bg-opacity-30 transition"
                  title="Share game"
                >
                  <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                    <path d="M15 8a3 3 0 10-2.977-2.63l-4.94 2.47a3 3 0 100 4.319l4.94 2.47a3 3 0 10.895-1.789l-4.94-2.47a3.027 3.027 0 000-.74l4.94-2.47C13.456 7.68 14.19 8 15 8z" />
                  </svg>
                </button>
                
                {/* ËØÑËÆ∫ÊåâÈíÆ */}
                <button 
                  onClick={handleComment}
                  className="p-2 bg-white bg-opacity-20 rounded-full hover:bg-opacity-30 transition"
                  title="Comments"
                >
                  <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                    <path fillRule="evenodd" d="M18 13V5a2 2 0 00-2-2H4a2 2 0 00-2 2v8a2 2 0 002 2h3l3 3 3-3h3a2 2 0 002-2zM5 7a1 1 0 011-1h8a1 1 0 110 2H6a1 1 0 01-1-1zm1 3a1 1 0 100 2h6a1 1 0 100-2H6z" clipRule="evenodd" />
                  </svg>
                </button>
                
                {/* ÊúÄÂ§ßÂåñÊåâÈíÆ */}
                <button 
                  onClick={toggleFullscreen}
                  className="p-2 bg-white bg-opacity-20 rounded-full hover:bg-opacity-30 transition"
                  title={isFullscreen ? "Exit fullscreen" : "Fullscreen"}
                >
                  <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                    {isFullscreen ? (
                      <path fillRule="evenodd" d="M4 2a1 1 0 011 1v2h2a1 1 0 010 2H5a1 1 0 01-1-1V3a1 1 0 011-1zm0 16a1 1 0 01-1-1v-2H1a1 1 0 110-2h2a1 1 0 011 1v2a1 1 0 01-1 1zm16 0a1 1 0 01-1-1v-2h-2a1 1 0 110-2h2a1 1 0 011 1v2a1 1 0 01-1 1zm0-16a1 1 0 011 1v2h2a1 1 0 110 2h-2a1 1 0 01-1-1V3a1 1 0 011-1z" clipRule="evenodd" />
                    ) : (
                      <path fillRule="evenodd" d="M3 4a1 1 0 011-1h4a1 1 0 010 2H5v3a1 1 0 01-2 0V4zm0 12a1 1 0 012 0v-3h3a1 1 0 110 2H4a1 1 0 01-1-1zm12 0a1 1 0 01-1 1h-4a1 1 0 010-2h3v-3a1 1 0 112 0v4zm0-12a1 1 0 00-1-1h-4a1 1 0 100 2h3v3a1 1 0 102 0V4z" clipRule="evenodd" />
                    )}
                  </svg>
                </button>
              </div>
            </div>
            
            {/* Ê∏∏ÊàèÂÜÖÂÆπÂå∫Âüü - Ê®°ÊãüÂõæÁâá‰∏≠ÁöÑÊ∏∏ÊàèÁïåÈù¢ */}
            <div className={`absolute inset-0 ${isFullscreen ? 'top-0' : 'top-16'}`}>
              {/* Ê®°ÊãüÊ∏∏ÊàèÂÜÖÂÆπ - ‰ΩøÁî®‰∏éÂõæÁâáÁõ∏‰ººÁöÑÂ∏ÉÂ±Ä */}
              <div className="w-full h-full bg-green-100 relative" style={{ background: 'linear-gradient(to bottom, #d1f2eb, #a9dfbf)' }}>
                {/* Ê∏∏ÊàèÂÜÖÂÆπËÉåÊôØ */}
                <div className="absolute inset-0">
                  {/* Â∑¶‰æßÁôΩËâ≤ÊäòÁ∫∏‰ºû */}
                  <div className="absolute top-10 left-8 w-16 h-16 bg-white rounded-full shadow-lg flex items-center justify-center">
                    <div className="w-12 h-12 bg-gray-200 rounded-full"></div>
                  </div>
                  
                  {/* Â∑¶‰æßÊòüÊòüË£ÖÈ•∞ */}
                  <div className="absolute top-24 left-16 text-yellow-400 text-2xl">‚òÖ</div>
                  
                  {/* Âè≥‰æßÊ£ïËâ≤Ê≥∞Ëø™ÁÜä */}
                  <div className="absolute top-12 right-16 w-14 h-14 bg-amber-800 rounded-full shadow-lg"></div>
                  
                  {/* Âè≥‰æßÊòüÊòüË£ÖÈ•∞ */}
                  <div className="absolute top-24 right-20 text-yellow-400 text-2xl">‚òÖ</div>
                  
                  {/* Â∫ïÈÉ®‰∏≠Â§ÆÈí•ÂåôÂõæÊ†áÂíå"-1" */}
                  <div className="absolute bottom-8 left-1/2 transform -translate-x-1/2 flex items-center space-x-2 bg-white/80 px-4 py-2 rounded-lg shadow-md">
                    <div className="w-6 h-6 bg-yellow-400 rounded-sm"></div>
                    <span className="font-bold text-gray-800 text-lg">-1</span>
                  </div>
                  
                  {/* Ê∏∏Êàè‰∏ªÂå∫Âüü */}
                  <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 w-3/4 h-48 bg-white/90 rounded-lg shadow-inner border-2 border-gray-300"></div>
                </div>
                
                {/* Â¶ÇÊûúÊèê‰æõ‰∫ÜiframeÊ∫êÔºåÂàôÊòæÁ§∫iframe */}
                {iframeSrc && (
                  <iframe
                    src={iframeSrc}
                    width="100%"
                    height="100%"
                    className="w-full h-full border-0"
                    allow="autoplay; fullscreen"
                    allowFullScreen
                    title={`${title} Online Game`}
                  />
                )}
              </div>
            </div>
            
            {/* Â∫ïÈÉ®ÊéßÂà∂Êù° - ÂÆåÂÖ®ÊåâÁÖßÂõæÁâáÊ†∑Âºè */}
            <div className="absolute bottom-0 left-0 right-0 z-10 flex justify-between items-center p-4 bg-gradient-to-t from-gray-900 to-transparent text-white h-16">
              <div className="flex items-center space-x-3 bg-gray-800/80 px-3 py-2 rounded-lg">
                <div className="flex items-center space-x-2">
                  <span className="text-sm font-medium">Rate this game:</span>
                  <div className="flex">
                    {[1, 2, 3, 4, 5].map((star) => (
                      <button 
                        key={star}
                        className="text-yellow-400 hover:text-yellow-300"
                        onClick={() => setRating(star)}
                        onMouseEnter={() => setHoverRating(star)}
                        onMouseLeave={() => setHoverRating(0)}
                        title={`Rate ${star} star${star !== 1 ? 's' : ''}`}
                      >
                        <svg 
                          xmlns="http://www.w3.org/2000/svg" 
                          className={`h-5 w-5 ${star <= (hoverRating || rating) ? 'fill-current' : ''}`}
                          viewBox="0 0 20 20" 
                          fill={star <= (hoverRating || rating) ? "currentColor" : "none"}
                          stroke="currentColor"
                        >
                          <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
                        </svg>
                      </button>
                    ))}
                  </div>
                </div>
              </div>
              
              <div className="flex space-x-3">
                <button 
                  className="text-white hover:text-blue-300 transition bg-gray-800/80 p-2 rounded-lg"
                  title="Report issue"
                >
                  <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                    <path fillRule="evenodd" d="M18 10c0 3.866-3.582 7-8 7a8.841 8.841 0 01-4.083-.98L2 17l1.338-3.123C2.493 12.767 2 11.434 2 10c0-3.866 3.582-7 8-7s8 3.134 8 7zM7 9H5v2h2V9zm8 0h-2v2h2V9zM9 9h2v2H9V9z" clipRule="evenodd" />
                  </svg>
                </button>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* ÁÆÄÁü≠ÂâØÊ†áÈ¢òÂíåÊèèËø∞ */}
      <p className="text-lg text-gray-700 mb-6">{subtitle}</p>
      <p className="text-gray-600 mb-12">{description}</p>

      {/* Overview */}
      <section className="mb-12">
        <h2 className="text-2xl font-bold mb-4">{title} Overview</h2>
        <p className="text-gray-700 leading-relaxed">{overview}</p>
      </section>

      {/* What is */}
      <section className="mb-12">
        <h2 className="text-2xl font-bold mb-4">What is {title}?</h2>
        <p className="text-gray-700 leading-relaxed">{whatIs}</p>
      </section>

      {/* How to Play */}
      <section className="mb-12">
        <h2 className="text-2xl font-bold mb-4">How to Play {title}</h2>
        <p className="text-gray-700 leading-relaxed">{howToPlay}</p>
      </section>

      {/* È¢ùÂ§ñÁöÑÂ∞èËäÇÔºàÊâ©Â±ïÁî®Ôºâ */}
      {sections && sections.length > 0 && (
        <div className="space-y-10 mb-12">
          {sections.map((s, i) => (
            <div key={i}>
              <h3 className="text-xl font-semibold mb-3">{s.heading}</h3>
              <p className="text-gray-600">{s.content}</p>
            </div>
          ))}
        </div>
      )}

      {/* ÂàÜ‰∫´Ê®°ÊÄÅÊ°Ü */}
      {showShareModal && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-gray-800 rounded-lg shadow-xl max-w-md w-full">
            <div className="p-6">
              <h3 className="text-xl font-bold text-white mb-4 text-center">Share this game</h3>
              
              {/* Á§æ‰∫§Â™í‰ΩìÂõæÊ†á */}
              <div className="flex justify-center space-x-4 mb-6">
                {[
                  { platform: 'facebook', icon: 'f', color: 'bg-blue-600', label: 'Facebook' },
                  { platform: 'twitter', icon: 'X', color: 'bg-black', label: 'X (Twitter)' },
                  { platform: 'whatsapp', icon: 'üí¨', color: 'bg-green-500', label: 'WhatsApp' },
                  { platform: 'linkedin', icon: 'in', color: 'bg-blue-500', label: 'LinkedIn' },
                  { platform: 'reddit', icon: 'R', color: 'bg-orange-500', label: 'Reddit' }
                ].map((social) => (
                  <button
                    key={social.platform}
                    onClick={() => window.open(`https://${social.platform}.com`, '_blank')}
                    className={`w-12 h-12 rounded-full ${social.color} text-white flex items-center justify-center text-lg font-bold hover:opacity-90 transition-opacity`}
                    title={`Share on ${social.label}`}
                    aria-label={`Share on ${social.label}`}
                  >
                    {social.icon}
                  </button>
                ))}
              </div>
              
              {/* ÈìæÊé•Â§çÂà∂Âå∫Âüü */}
              <div className="flex mb-4">
                <input
                  type="text"
                  value={typeof window !== 'undefined' ? window.location.href : ''}
                  readOnly
                  className="flex-1 px-3 py-2 bg-gray-700 text-white rounded-l-lg border border-gray-600 text-sm"
                />
                <Button
                  onClick={copyToClipboard}
                  className="bg-purple-600 hover:bg-purple-700 text-white px-4 rounded-l-none"
                >
                  Copy Link
                </Button>
              </div>
              
              <Button
                onClick={() => setShowShareModal(false)}
                variant="outline"
                className="w-full bg-gray-700 hover:bg-gray-600 text-white"
              >
                Close
              </Button>
            </div>
          </div>
        </div>
      )}

      {/* ËØÑËÆ∫Âå∫Âüü */}
      {showComments && (
        <div className="w-full max-w-4xl mx-auto mb-16 bg-white p-6 rounded-lg shadow-md">
          <h3 className="text-xl font-bold mb-4">Comments</h3>
          <p className="text-gray-600 mb-4">Comments feature would be implemented here.</p>
          <Button
            onClick={() => setShowComments(false)}
            className="bg-blue-500 hover:bg-blue-600 text-white"
          >
            Close Comments
          </Button>
        </div>
      )}

      {/* Â≠êÁªÑ‰ª∂ÔºåÊØîÂ¶Ç Levels */}
      {children}
    </div>
  );
}